<template>
    <div class="login_container">
        <div class="login_box">
            <!-- 头像 -->
            <div class="avatar_box">
                <img src="../../assets/logo/logo.png" alt="">
            </div>
            <!-- 登录表单区 -->
            <el-form :model="from" :rules="rules" label-width="0px" class="login_from" ref="loginFormRef">
                <el-form-item prop="username">
                    <el-input v-model="from.username" prefix-icon="el-icon-user" ></el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input v-model="from.password " prefix-icon="el-icon-lock"  type="password" show-password></el-input>
                </el-form-item>
                <el-form-item class="btns">
                    <el-button type="primary" @click="login">登录</el-button>
                    <el-button type="info" @click="resetLoginForm">重置</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name:'ProjectOne',
    data() {
        return {
            from:{
                username:'admin',
                password:'123456'
            },
            // 表单验证规则
            rules:{
                username:[
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
                ],
                password:[
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        resetLoginForm(){
            this.$refs.loginFormRef.resetFields()
        },
        login(){
            // 登录前预校验表单信息
            this.$refs.loginFormRef.validate(valid=>{
                if(!valid) return false;
                // 校验完成后发送请求
                // axios.post('https://lianghj.top:8888/api/private/v1/login',this.from)
                axios({
                    method:'post',
                    url:'login',
                    data:{...this.from}
                })
                .then(result=>{
                    if(result.data.meta.status!==200) throw new error(result.data.meta.msg)
                    
                    // console.log(result);
                    this.$message.success('登录成功')
                    window.sessionStorage.setItem('token',result.data.data.token)
                    this.$router.push('/OneHome')
                })
                .catch(err=>{
                    this.$message.error('登录失败',err)
                })
            })
        }
    },
}
</script>

