import Vue from "vue";
import VueRouter from "vue-router";
import OneLogin from '../pages/ProjectOne/OneLogin.vue'
import OneHome from '../pages/ProjectOne/OneHome.vue'
import Welcome from '../pages/ProjectOne/Welcome.vue'
import Users from '../pages/ProjectOne/user/Users.vue'
import Rights from '../pages/ProjectOne/power/Rights.vue'
import Roles from '../pages/ProjectOne/power/Roles.vue'
import Cate from '../pages/ProjectOne/goods/Cate.vue'
import Params from '../pages/ProjectOne/goods/Params.vue'
import Goods from '../pages/ProjectOne/goods/Goods.vue'
import Order from '../pages/ProjectOne/order/Order.vue'
import Report from '../pages/ProjectOne/report/Report.vue'

Vue.use(VueRouter)

const router=new VueRouter({
    routes:[
        { 
            path:'/ProjectOne',
            component:OneLogin
        },
        {
            path:'/OneHome',
            component:OneHome,
            redirect:'/welcome',
            children:[
                {
                    path:'/welcome',
                    component:Welcome
                },
                {
                    path:'/users',
                    component:Users
                },
                {
                    path:'/rights',
                    component:Rights
                },
                {
                    path:'/roles',
                    component:Roles
                },
                {
                    path:'/categories',
                    component:Cate
                },
                {
                    path:'/params',
                    component:Params
                },
                {
                    path:'/goods',
                    component:Goods
                },
                {
                    path:'/orders',
                    component:Order
                },
                {
                    path:'/reports',
                    component:Report
                },

            ]
        },
    ]
})
// 挂载路由导航守卫
router.beforeEach((to, from, next) => {
    if(to.path==='/ProjectOne') return next()
    const tokenstr=window.sessionStorage.getItem('token')
    if(!tokenstr) return  next('/ProjectOne')
    next()
})

export default router